<template>
    <div id="experience">
          <h3>{{title}}</h3><hr/>
        <ul class="wrap">
            <li class="item" v-for="(item,i) in projects" :key="i">
                <p><span></span>{{item.name}}</p>
                <ol class="desc"> 
                    <li v-for="(desc,i) in item.desc" :key="i">
                        {{desc}}
                    </li>
                </ol>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"Experience",
    data(){
        return{
            title:"项目经历",
            projects:[
                {
                    name:"进销存系统开发",
                    desc:[
                        "进销存系统的需求以及确定和熟悉所使用的技术并进行系统的规划设计;",
                        "jQuery和easyUI完成前端界面布局和交互功能开发,后端采用jsp技术；",
                        "MySQL数据库,编写SQL语句完成系统的数据操作。"
                    ]
                },
                {
                    name:"网易新闻移动端应用开发",
                    desc:[
                        "用Node搭建vue开发环境,npm安装相关依赖；",
                        "用vux 进行页面布局编写；",
                        "用axios的jsonp方式获取新闻数据，并展示在页面。"
                    ]
                }
                
            ]
        }
    },
}
</script>

<style scoped>
    #experience{
        margin-top: 30px;
    }
    .item{
        margin-top:10px;
    }
    .item p{
        font-size: 17px;
        font-weight: 600;
        letter-spacing: 0px;
    }
    .item p span{
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #000;
        margin-right: 10px;
    }
    .desc{
        padding-top:10px;
       
    }
    .desc li{
        text-indent: 2em;
         list-style:decimal inside;
    }
</style>




