<template>
    <div id="skill">
        <h3>{{title}}</h3><hr/>
        <ul class="wrap">
            <li class="item" v-for="(item,i) in category" :key="i">
                <p><span></span>{{item.name}}</p>
                <ul class="desc"> 
                    <li v-for="(desc,i) in item.desc" :key="i">
                        {{desc}}
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"Skill",
    data(){
        return{
            title:"专业技能",
            category:[
                {
                    name:"VUE",
                    desc:["有vue开发经历，熟悉运用vue-router，axios相关技术编写前端页面"]
                },
                 {
                    name:"HTML5，CSS3  & JavaScript",
                    desc:["熟练掌握css3、html5,会用原生js独立制作网页"]
                },
                 {
                    name:"移动端开发",
                    desc:["会使用rem、vw/wh、媒体查询等进行移动端适配；熟悉了解微信小程序的开发"]
                },
                 {
                    name:"数据库",
                    desc:["熟悉了解mysql、SQL server,掌握常用的SQL语句操作数据库"]
                },
                 {
                    name:"后端语言",
                    desc:["熟悉了解Java语言，有过用jsp/servlet等写网站后台"]
                },
                 {
                    name:"其他",
                    desc:["通过大学英语六级考试,具有一定的英文文档阅读能力"]
                },
            ]
        }
    },
}
</script>

<style scoped>
    #skill{
        margin-top:30px;
    }
    .item{
        margin-top:10px;
    }
    .item p{
        font-size: 17px;
        font-weight: 600;
        letter-spacing: 0px;
    }
    .item p span{
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #000;
        margin-right: 10px;
    }
    .desc{
        padding-top:10px;
    }
    .desc li{
        text-indent: 2em;
    }
</style>




