<template>
    <div id="education">
        <h3>{{title}}</h3><hr/>
        <div class="content">
            <p class="college">
                <span>{{college}}</span>
                <span>{{rank}}</span>
            </p>
            <p class="specialty">{{specialty}}</p>
            <div class="course">
                <h4>相关课程：</h4>
                <p>{{courseInfo}}</p>
            </div>
        </div>

    </div>
</template>
<script>
export default {
    name:"Education",
    data(){
        return{
            title:"教育经历",
            college:"岭南师范学院（2015.09~2019.06)",
            rank:"本科",
            specialty:"信息管理系 |  信息管理与信息系统专业",
            courseInfo:"Java编程基础、C#、jsp、数据结构、网页设计、erp原理与实践、软件项目管理、sql server、oracle、计算机网络等"

        }
    },
}
</script>

<style scoped>
    #education{
        margin-top:30px;
    }
    .college{
        margin:15px 0;
    
    }
    .college span:nth-child(2){
        float: right;
    }
    .specialty{
        margin:15px 0;
      
    }
    .course h4{
        font-size: 18px;

    }
    .course p{
        text-indent:2em;
    }
</style>




