<template>
<div class="header">

  <h1>{{name}}</h1>
  <div class="personal-info">
    <p><span>电话：{{phone}}</span>|<span>现居：{{addr}}</span></p>
    <p><span>邮箱：{{mail}}</span>|<span>生日：{{birth}}</span></p>
    <p><span>{{job}}</span>|<span>求职意向：{{target}}</span></p>
    <p><span> <a class="link" :href="github">GitHub Link</a>  </span></p>
  </div>

</div>
 
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      name:"钟永杰",
      phone:"18028421410",
      birth:"1996",
      addr:"广州天河区",
      mail:"13432803741@163.com",
      job:"应届生",
      target:"web前端开发",
      github:"https://github.com/jiesue"
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header{text-align: center;}
  h1{
    color:black;
    font-size: 22px;
    font-weight: bold;
    padding-bottom: 20px;
  }
  .personal-info{
    font-size: 18px;
    color:rgb(47, 6, 80);

  }
  span{
   margin: 20px;
  }
  .link{
    display: inline-block;
    width: 130px;
    height: 30px;
    margin-top:10px;
    color:rgb(47, 6, 80);
    line-height: 30px;
    border:1px inset rgb(47, 6, 80);
    border-radius: 5px;
    transition-duration: 1s;
  
  }
  .link:hover{
    border-color:#f40;
    color:#f40;
  }
</style>



